const logger = require('loglevel');
const NexmoClientError = require('../../conversationClientError').NexmoClientError;

/**
 * TraceWS connects the WebSocket and reports the webRTC trace
 * @param params.rtcstatsUri the Uri of the websocket service to send the report
 * @private
 */
class TraceWS {
    constructor() {
        this.log = logger.getLogger(this.constructor.name);

        this.ws_config = {
            PROTOCOL_VERSION: '1.0',
            MAX_BUFFER_LENGTH: 50,
            MAX_RECONNECT_RETRIES: 5,
            RECONNECT_RETRY_TIMEOUT: 5000
        }
        this.buffer = [];
        this.reconnectRetriesCount = 0;
    }

    init(params) {
        this.rtcstatsUri = params.rtcstatsUri;
        this.reconnect();
    }

    reconnect() {
        this.log('TRACE-WS reconnect', this.connection ? this.connection.readyState : 'no connection');
        if (this.connection && this.connection.readyState !== WebSocket.CLOSED) {
            this.connection.close();
        }
        delete this.connection;
        if (this.reconnectRetriesCount > this.ws_config.MAX_RECONNECT_RETRIES) {
            return;
        }
        this.reconnectRetriesCount++;
        if (this.rtcstatsUri) {
            this.connection = new WebSocket(this.rtcstatsUri, this.ws_config.PROTOCOL_VERSION);
            this.connection.onerror = (e) => {
                this.log('TRACE-WS onerror', e);
            };

            this.connection.onopen = () => {
                this.reconnectRetriesCount = 0;
                this.log('TRACE-WS onopen', this.buffer.length);
                while (this.buffer.length) {
                    this.connection.send(JSON.stringify(this.buffer.shift()));
                }
            };

            this.connection.onclose = (event) => {
                this.log('TRACE-WS onclose', event);
                setTimeout(() => {
                    this.reconnect();
                }, this.ws_config.RECONNECT_RETRY_TIMEOUT);
            };
        }
        else {
            throw new NexmoClientError('reporting rtcstatsUri not set use init( {rtcstatsUri:""} )');
        }
    }

    trace() {
        let args = Array.prototype.slice.call(arguments);
        args.push(new Date().getTime());
        if (this.connection && this.connection.readyState === WebSocket.OPEN) {
            this.connection.send(JSON.stringify(args));
        } else {
            if (this.buffer.length <= this.ws_config.MAX_BUFFER_LENGTH) {
                this.buffer.push(args);
            }
        }
    }

    disable() {
        this.connection.close();
    }
}
module.exports = TraceWS;
